/* Chatroom.css */
.chatroom {
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 5px;
  max-width: 600px;
  margin: 20px auto;
}

.messages {
  margin-bottom: 20px;
  max-height: 300px;
  overflow-y: scroll;
}

.message {
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.message strong {
  color: #333;
}

.message-form input {
  padding: 10px;
  width: 80%;
  margin-right: 10px;
}

.message-form button {
  padding: 10px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
}

.message-form button:hover {
  background-color: #0056b3;
}


  